import React from 'react';
import {
  Avatar,
  Chip, IconButton,
  ListItem,
  Paper,
  ListItemAvatar,
  ListItemSecondaryAction,
  ListItemText, Tooltip,
  Typography
} from "@material-ui/core";
import FileCopyIcon from "@material-ui/icons/FileCopy";
import SearchIcon from "@material-ui/icons/Search";
import {copy2clip} from "../FWrapped";

function IconInfo(props) {

  const {name, chain, logo, contractAddress, scannerUrl} = props;

  console.log(name, chain, logo, contractAddress, scannerUrl);

  return (
    <Paper className="IconInfo">
      <ListItem>
        <ListItemAvatar>
          <Avatar>
            <img src={logo} width={40} height={40}/>
          </Avatar>
        </ListItemAvatar>
        <ListItemText
          primary={<Typography component="div">{name} <Chip variant="outlined" size="small" className={chain} label={chain}/></Typography>}
          secondary={<Typography variant="caption">{contractAddress}</Typography>}
        />
        <ListItemSecondaryAction>
          <Tooltip title="Copy Contract Address">
            <IconButton edge="end" aria-label="delete"
                        onClick={() => {
                          copy2clip(contractAddress, this)
                        }}>
              <FileCopyIcon
              />
            </IconButton>
          </Tooltip>
          <Tooltip title="View on Scanner">
            <IconButton edge="end" aria-label="delete"
                        onClick={() => {
                          window.open(scannerUrl);
                        }}>
              <SearchIcon/>
            </IconButton>
          </Tooltip>
        </ListItemSecondaryAction>
      </ListItem>
    </Paper>
  );
}

export default IconInfo;
